<template>
  <div>
    <div class="question-text lightest-bg" v-for="(item,i) in questionList.dataSet" :key="i">
      <div class="text-top">
        <img :src="item.questionAvatar " alt v-if="item.questionAvatar !==null">
        <!-- <span class="iconfont icon-lihansongjing questionAvatar" v-else></span> -->
        <img src="https://mhb.hanyujingyun.cn/images/lihansongjing.svg" v-else>
        <span class="name light-text">{{item.questionNickname?item.questionNickname:'匿名'}}</span>
        <!-- <span class="name_time">{{item.timecreated}}</span> -->
        <div class="show">
          <span class="Qs darkest-text">{{item.questionContent}}</span>
        </div>
      </div>
      <div class="center">
        <div v-html="item.answerContent" class="center-text medium-text"></div>
      </div>
      <!-- <div class="center" v-else>
        <div class="answerAudio theme-bg">
          <div class="answerAudio-box">
            <div class="answerAudio-btn" @click="play(item,i)">
              <span class="iconfont icon-playingIcon" v-show="flag==false"></span>
              <span class="iconfont icon-playIconsm" v-show="flag==true"></span>
            </div>
            <span class="timer">{{timer}}秒</span>
          </div>
        </div>
      </div>-->
      <div class="qrTime">
        <span class="time light-text">{{item.timecreated}}</span>
      </div>
      <div class="text-bottom">
        <ul>
          <li>
            <a href="javascript:void(0)" @click="share(item,i)">
              <span class="iconfont icon-fenxiang light-text"></span>
              <span class="light-text">分享</span>
            </a>
          </li>
          <li>
            <a href="javascript:void(0)" @click="collect(item,i)">
              <span
                class="iconfont icon-shoucang light-text"
                :class="[item.is_collect===1?'show':'']"
              ></span>
              <span class="light-text" v-html="item.is_collect==1?'已收藏':'收藏'"></span>
            </a>
          </li>
        </ul>
      </div>
    </div>
    <el-dialog
      :title="qrTitle"
      :visible.sync="shareShow"
      id="scanDialoge"
      :close-on-click-modal="false"
      show-close
    >
      <QrcodeVue :value="qrText" size="200" class="qrCode"/>
      <!-- <img id="qrCode" :src="imgUrl" alt> -->
      <p>{{tipInfo}}</p>
    </el-dialog>
  </div>
</template>
<script>
import QrcodeVue from "qrcode.vue";
export default {
  props: ["questionList"],
  data() {
    return {
      qrTitle: "",
      tipInfo: "",
      qrText: "",
      shareShow: false,
      // isShow: false,
      flag: true,
      onShow: false
    };
  },
  mounted() {},
  methods: {
    play(item, i) {
      var audio = document.getElementById("myAudio");
      audio.src = item.answerAudio.sq;
      if (this.flag == true) {
        this.flag = false;
        audio.play();
      } else {
        this.flag = true;
        audio.pause();
      }
    },
    collect(item, i) {
      var _this = this;
      _this.isShow = !_this.isShow;
      let data = {
        type: "question",
        target: item.questionId,
        operation: "collect"
      };
      var url = this.APIURL + "/classic/question/behavior";
      this.$axios
        .post(url, data, { headers: { SessionKey: this.SessionKey,key:this.key } })
        .then(function(data) {
          if (data.data.errorCode === 0) {
            _this.$emit("showClick");
          } else {
            alert(data.data.msg);
            _this.$router.push({ path: "/login" });
          }
        });
    },
    share(item, i) {
      // console.log(item);
      this.shareShow = true;
      this.qrText =
        "https://m.lihan.ltd/#/pages/QA/QAdetail?curId=" + item.questionId;
      // "&app=false&bgclr=false";
      this.qrTitle = "微信扫描二维码进行分享";
    }
  },
  watch: {
    timer() {}
  },
  computed: {
    SessionKey() {
      return this.$store.state.SessionKey;
    },
    key() {
      return this.$store.state.key;
    },
    timer(min, max) {
      return Math.round(Math.random() * 10 + 10);
    }
  },
  components: {
    // myBreadcrumb,
    QrcodeVue
  }
};
</script>
<style lang="scss">
.question-text {
  width: 100%;
  height: 237px;
  overflow: hidden;
  margin-bottom: 10px;
  position: relative;

  .text-top {
    .name {
      opacity: 1;
      font-size: 14px;
      font-family: PingFang SC;
      letter-spacing: 0px;
      margin: 24px 0 0 10px;
      position: relative;
      top: -5px;
    }
    img {
      width: 22px;
      height: 22px;
      margin: 16px 0 0 14px;
    }
    .show {
      overflow: hidden;
      .Qs {
        margin: 12px 0 0 45px;
        opacity: 1;
        font-size: 18px;
        font-family: Adobe Heiti Std;
        line-height: 24px;
        letter-spacing: 0px;
        display: block;
      }
    }
    .questionAvatar {
      font-size: 22px;
      display: inline-block;
      margin: 16px 0 0 14px;
      vertical-align: sub;
    }
  }
  .center {
    padding: 10px 10px 0 45px;
    .center-text {
      opacity: 1;
      font-size: 14px;
      font-family: Microsoft YaHei;
      line-height: 20px;
      letter-spacing: 0px;
      display: inline-block;
      margin: 0;

      p {
        margin: 0;
      }
    }
    .answerAudio {
      width: 215px;
      height: 40px;
      border-top-right-radius: 36px;
      border-bottom-right-radius: 36px;
      border-top-left-radius: 36px;
      padding: 2px 20px;
      margin-top: 30px;
    }
    .answerAudio-box {
      position: relative;
      width: 100%;
      height: 100%;
      .answerAudio-btn {
        display: inline-block;
        .iconfont {
          font-size: 25px;
          color: #f3f3f3;
          line-height: 40px;
          vertical-align: middle;
        }
      }
      .timer {
        color: #f3f3f3;
        vertical-align: middle;
        margin-left: 10px;
        font-size: 18px;
        display: inline-block;
      }
    }
  }
  .qrTime {
    position: absolute;
    bottom: 8px;
    left: 45px;
    .time {
      font-size: 14px;
      font-family: PingFang SC;
      letter-spacing: 0px;
    }
  }
  .text-bottom {
    overflow: hidden;
    position: absolute;
    bottom: 0;
    right: 0;
    ul {
      margin: 0;
      padding: 0;
      line-height: 36px;
      overflow: hidden;
      li {
        list-style: none;
        float: left;
        margin-right: 20px;
        a {
          text-decoration: none;
          .iconfont {
            font-size: 16px;
            margin-right: 3px;
            vertical-align: middle;
          }
          .show {
            color: #584945 !important;
          }
          span:nth-child(2) {
            font-size: 16px;
            vertical-align: middle;
          }
        }
      }
    }
  }
}
</style>
