<template>
  <div class="qr_content">
    <div class="content_left">
      <div class="tabs">
        <qrTemplate :questionList="questionList" @showClick="giveList"></qrTemplate>
      </div>
    </div>
    <div class="content_right" id="advertsPlace">
      <div class="inner" :style="{top:scrollTop+'px'}">
        <div class="question-loading lightest-bg">
          <a href="javascript:void(0)" @click="setQaShow" class="theme-text">
            <span class="iconfont icon-edit theme-text"></span>
            <p>发起提问</p>
          </a>
        </div>
        <advertisingTab/>
        <div class="blank">
          <a href="https://mp.weixin.qq.com/s/ENqQJn9524x6oSxD7g3tWQ" target="_blank">
            <img src="../../assets/img/guanggao2.png" class="imgs">
          </a>
        </div>
      </div>
    </div>
    <qrdialog/>
  </div>
</template>
<script>
import qrdialog from "../q&Answer/components/qr-dialog";
import qrTemplate from "../q&Answer/components/qr-template";
import advertisingTab from "../../components/advertising-tab";
export default {
  data() {
    return {
      activeName: "jingxuan",
      // centerDialogVisible: false,
      textarea: "",
      questionList: "",
      scrollTop: 0
    };
  },
  methods: {
    giveList(type) {
      var _this = this;
      let data = {
        type: "recommend"
      };
      var url = this.APIURL + "/classic/question/all";
      this.$axios
        .post(url, data, { headers: { SessionKey: this.SessionKey,key:this.key } })
        .then(function(res) {
          _this.questionList = res.data.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    setQaShow() {
      this.$store.commit("setQadidlogShow", true);
    }
  },
  mounted() {
    var _this = this;
    this.giveList();
    let offsetTop = document.getElementById("advertsPlace").offsetTop;
    document.onscroll = function(e) {
      let scrollTop = document.documentElement.scrollTop;
      if (scrollTop > offsetTop) {
        let diff = scrollTop - offsetTop;
        _this.scrollTop = diff;
      } else {
        _this.scrollTop = 0;
      }
    };
  },
  computed: {
    SessionKey() {
      return this.$store.state.SessionKey;
    },
    key() {
      return this.$store.state.key;
    }
  },
  setOffset(val) {
    this.scrollTop = val;
  },
  components: {
    qrTemplate,
    advertisingTab,
    qrdialog
  }
};
</script>
<style lang="scss">
.qr_content {
  width: 1200px;
  margin: 0 auto;
  margin-top: 14px;
  overflow: hidden;
  display: flex;

  // border: 1px solid red;
  .content_left {
    .tabs {
      width: 920px;
      overflow: hidden;
    }
  }
  .content_right {
    margin-left: 13px;
    position: relative;
    height: auto;
    width: 265px;

    .inner {
      position: absolute;
      width: 100%;
      .question-loading {
        width: 100%;
        height: 120px;
        overflow: hidden;
        a {
          display: block;
          width: 100px;
          height: 100px;
          margin: 10px auto 0;
          overflow: hidden;
          border: 1px solid;
          border-radius: 50%;
          text-align: center;
          padding: 20px 18px 20px 18px;
          text-decoration: none;
          span:nth-child(1) {
            opacity: 1;
            font-size: 16px;
            font-family: Adobe Heiti Std;
            letter-spacing: 0px;
            display: inline-block;
            margin-bottom: 2px;
          }
          p {
            margin: 0;
            font-size: 14px;
          }
        }
      }

      .blank {
        width: 100%;
        height: 100%;
        opacity: 1;
        margin-top: 10px;
        position: relative;
        .imgs {
          width: 100%;
          height: 670px;
          //    animation: ghostUpdown 6s infinite alternate;
          //  -webkit-animation: ghostUpdown 6s infinite alternate;
        }
      }
    }
    @keyframes ghostUpdown {
      from {
        top: 0;
      }
      to {
        top: 300px;
      }
    }
    @-webkit-keyframes ghostUpdown {
      from {
        top: 300px;
      }
      to {
        top: 0;
      }
    }
    .el-tabs__header {
      margin: 0 30px 3px;
    }

    .tabs__nav-wrap::after {
      width: 0;
      height: 0;
    }
  }
}
.el-tabs__nav-wrap.is-scrollable.is-top::after {
  width: 0 !important;
  height: 0 !important;
}

.el-textarea__inner {
  background-color: #eeeeee;
}
// .el-dialog--center .el-dialog__body {
//   padding: 0 25px 30px;
// }
</style>
